.header-section {
	position: relative;
	background: linear-gradient(to bottom, var(--color-1), var(--color-2));
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
	}
	.orn-22 {
		position: absolute;
		top: 0;
		left: 50%;
		width: 160px;
		transform: translate(-50%, 30%);
	}
	.orn-23 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100px;
		transform: translate(-50%, 10%);
	}
	.orn-24 {
		position: absolute;
		top: 0;
		right: 0;
		width: 25%;
		transform: translate(30%, -30%);
	}
	.orn-25 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 45%;
		transform: translate(-10%, 60%) rotate(10deg) scaleX(-1);
	}
	.orn-26 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 25%;
		transform: translate(-35%, 5%) rotate(45deg) scaleY(-1);
	}
	.orn-27 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 60%;
		transform: translate(50%, -20%) rotate(20deg) scaleY(-1);
	}
	.header-content {
		position: relative;
		width: 100%;
		height: 100vh;
		.header {
			position: relative;
			padding: 4rem 0 3rem;
			height: 100%;
			.header-title {
				position: relative;
				text-align: center;
				height: 100%;
				&-content {
					position: relative;
					display: flex;
					justify-content: center;
					align-items: center;
					width: 100%;
					height: 100%;
					gap: 2rem;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 75%;
				height: auto;
				aspect-ratio: 6/4;
				margin: 0 auto;
				.image-element {
					height: 100%;
					img {
						position: relative;
						width: 100%;
						height: 100%;
						border-radius: var(--border-radius);
					}
					.header-background {
						position: absolute;
						top: 50%;
						left: 50%;
						width: 90%;
						height: 90%;
						object-fit: cover;
						transform: translate(-50%, -50%);
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
