.gallery-section {
	position: relative;
	padding: 5rem 0;
	background: linear-gradient(to bottom, var(--color-4), var(--color-1));
	@include max-md {
		padding: 3rem 0;
	}
	.img-gallery {
		height: auto;
		width: 100%;
		aspect-ratio: 4/3;
		object-fit: cover;
	}
	.img-thumbnail {
		object-fit: cover;
		padding: 0;
		border: none;
		border-radius: 0;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
	}
	.splide {
		.splide__slide.is-active {
			.img-thumbnail {
				border: 3px solid #fff;
			}
		}
		.splide__arrow {
			border-radius: 0;
			width: 70px;
			height: 65px;
			background-color: transparent;
			@include max-lg {
				width: 50px;
				height: 40px;
			}
			svg {
				fill: var(--color-light);
				width: 35px;
				height: 40px;
				filter: drop-shadow(2px 2px 1px rgba(0, 0, 0, 0.6));
			}
			&--prev {
				left: 0;
				transform: translate(-100%, -50%);
			}
			&--next {
				right: 0;
				transform: translate(100%, -50%);
			}
		}
	}
}
