.countdown {
	display: flex;
	text-align: center;
	justify-content: center;
	position: relative;
	margin: 0 auto;
	width: fit-content;
	padding: 2rem 0;
	gap: 1rem;
	z-index: 3;
	@include max-md {
		gap: 0.5rem;
	}
	.days,
	.hours,
	.minutes,
	.seconds {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
		padding: .5rem 1rem 1rem;
		min-width: 150px;
		background-color: var(--color-3);
		border-radius: var(--border-radius);
		@include max-md {
			min-width: 65px;
			padding: 1rem 0.75rem;
		}
		@include max-xs {
			min-width: 63px;
		}
		@include max-rwd(350px) {
			min-width: 72px;
		}
		.huruf {
			font-size: 14px;
			font-weight: 600;
			@include max-xs {
				font-size: 12px;
			}
		}
		.angka {
			margin-bottom: 0;
			font-weight: 500;
			font-size: 38px;
			font-family: $font-secondary;
			@include max-md {
				font-size: 32px;
			}
			@include max-xs {
				font-size: 28px;
			}
		}
		.huruf,
		.angka {
			margin: 0;
			padding: 0;
		}
		.frame{
			position: absolute;
			top: 50%;
			left: 50%;
			width: 110%;
			transform: translate(-50%,-50%);
		}
	}
}
