.thank-section {
	position: relative;
	padding: 5rem 0 10rem;
	text-align: center;
	@include max-md {
		padding: 3rem 0 8rem;
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-2);
	color: var(--color-light);
	small {
		position: relative;
	}
}
