.wishes-section {
	position: relative;
	background: linear-gradient(to bottom, var(--color-1) 0%, var(--color-2) 75%, var(--color-1) 100%);
	.wishes-form-wrapper {
		position: relative;
		padding: 5rem 0 0;
		@include max-md {
			padding: 3rem 0 0;
		}
	}
	.card-form {
		background-color: transparent;
		&.card {
			border: none;
		}
		.card-body {
			padding: 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
			.btn-custom {
				min-width: 50%;
			}
		}
	}

	.wishes-preview {
		position: relative;
		padding: 0 0 5rem;
		@include max-md {
			padding: 0 0 3rem;
		}
		.card {
			position: relative;
			border: none;
			background-color: transparent;
			.card-body {
				padding: 2rem 0 0;
				.wishes {
					text-align: left;
					max-height: 32rem;
					overflow: auto;
					padding-right: 1rem;
					padding-left: 1.5rem;
					color: var(--color-dark);
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-4);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 1);
							position: relative;
							&::before {
								position: absolute;
								top: 0;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 1);
							}
							p,
							span,
							h6 {
								text-shadow: none;
							}
							h6 {
								font-weight: 700;
								font-size: 18px;
							}
						}
					}
				}
			}
		}
	}
}
