.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 5%;
		left: 0;
		width: 20%;
		transform: translate(-20%, 0);
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 22%;
		transform: translate(0, -35%);
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 45%;
		width: 140px;
		transform: translate(0, -35%);
	}
	.orn-4 {
		position: absolute;
		top: 0;
		left: 36%;
		width: 140px;
		transform: translate(0, -30%);
	}
	.orn-5 {
		position: absolute;
		top: 0;
		right: 64%;
		width: 16%;
		transform: translate(0, -20%);
	}
	.orn-6 {
		position: absolute;
		top: 0;
		left: 54%;
		width: 100px;
		transform: translate(0, -40%);
	}
	.orn-7 {
		position: absolute;
		top: 0;
		left: 60%;
		width: 21%;
		transform: translate(0, -30%);
	}
	.orn-8 {
		position: absolute;
		top: 0;
		left: 75%;
		width: 22%;
		transform: translate(0, -25%);
	}
	.orn-9 {
		position: absolute;
		top: 0;
		right: 0;
		width: 28%;
		transform: translate(35%, -15%);
	}
	.orn-10 {
		position: absolute;
		top: 40%;
		left: 20%;
		width: 35%;
	}
	.orn-11 {
		position: absolute;
		top: 0;
		right: 2%;
		width: 18%;
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 24%;
		transform: translate(27%, 30%) rotate(-10deg);
	}
	.orn-13 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 200px;
		transform: translate(-50%, 35%);
	}
	.orn-14 {
		position: absolute;
		bottom: 0;
		left: 55%;
		width: 80px;
	}
	.orn-15 {
		position: absolute;
		bottom: 0;
		left: 59%;
		transform: translate(0, 20%);
	}
	.orn-16 {
		position: absolute;
		bottom: 0;
		left: 68%;
		width: 320px;
		transform: translate(0, 28%);
	}
	.orn-17 {
		position: absolute;
		bottom: 0;
		right: 50%;
		width: 16%;
		transform: translate(0, 50%);
	}
	.orn-18 {
		position: absolute;
		bottom: 0;
		right: 63%;
		width: 10%;
		transform: translate(0, 5%);
	}
	.orn-19 {
		position: absolute;
		bottom: 0;
		right: 70%;
		width: 18%;
		transform: translate(0, 20%);
	}
	.orn-20 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 28%;
		transform: translate(-30%, 10%);
	}
	.orn-21 {
		position: absolute;
		bottom: 10%;
		left: 8%;
		width: 10%;
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 7rem;
		}
		.cover-content {
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				.cover-ornament {
					position: relative;
					width: 160px;
					margin: 0 auto 0.5rem;
				}
				.cover-description {
					padding: 1rem;
					text-align: center;
					.cover-event {
						position: relative;
						margin-bottom: 1rem;
					}
					.greeting-wrapper {
						position: relative;
						margin-bottom: 1rem;
					}
					.title {
						padding: 1rem 0;
						margin-bottom: 0;
					}
					p {
						margin-bottom: 0;
					}
					h6 {
						margin-bottom: 0;
						font-weight: 600;
					}
				}
			}
			.elements-widget {
				position: relative;
				width: 100%;
			}
		}
	}
}
